import React, {useEffect, useState} from 'react';
import { useSelector } from 'react-redux';

import SettingMenu from './SettingMenu';
import InformationTab from '../tab/InformationTab';
import AddFriendTab from '../tab/AddFriendTab';
import {getChatMembers} from "../../../../services/chatSerivce";


const SettingWindow = (props: any) => {
    const currentUserData = useSelector((state: any) => state.user)

    const [members, setMembers] = useState([])

    useEffect(() => {
        getChatMembers(props.chatData.data.id)
            .then((result) => {
                setMembers(result)
            })
    }, [])

    if (props.chatData && currentUserData.info) {
        return (
            <div className="container">
                <div>
                    <SettingMenu/>
                </div>
                <div className="card" style={{marginTop: "5px"}}>
                    <div className="tab-content" id="nav-tabContent">
                        <InformationTab chatData={props.chatData}/>
                        <AddFriendTab chatData={props.chatData} members={members}/>
                    </div>
                </div>
            </div>
        )
    } else {
        return (
            <div>
                Loading
            </div>
        )
    }
}

export default SettingWindow;
